.About article {
  margin: 1rem 0;
}

.About p {
  text-align: justify;
}

.About img {
  width: 75%;
  border-radius: 50%;
  border: 0.15rem solid #b12842;
}

.About h2 {
  text-align: center;
  color: #b12842;
  margin: 1rem 0;
}

.About figure {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 25.875rem) {
  .About article {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    margin: 1rem 0;
  }

  .About h2 {
    margin-bottom: 1rem;
  }
}
